import React from "react";
import "./index.less";
import { removeModel } from "./ModelBox";
/**
 *
 * @param {obj} props
 *
 * onRef [Function]: {open:close:} *visibel 
 * mask [Boolean]:  true
 * visibel [Boolean]:
 * closeBack [Function]:   visiblevisibel=false 
 * outAnmation [Boolean]:   true
 * queryBack [Function]:   value
 */

export default function InfoBox({ title = "", msg, address }) {
  return (
    <div className="my_info_model an_start_move">
      <p className="title">{title}</p>
      <div className="my_info_box">
        <div className="label"></div>
        <div className="right_box">{address}</div>
      </div>
      {msg && (
        <div className="my_info_box">
          <div className="label"></div>
          <div className="right_box">{msg}</div>
        </div>
      )}
      <button className="bot_btn" type="button" onClick={() => removeModel()}>
        
      </button>
    </div>
  );
}
