import React from "react";
import "./index.less";
import { removeModel } from "./ModelBox";
/**
 *
 * @param {obj} props
 *
 * onRef [Function]: {open:close:} *visibel 
 * mask [Boolean]:  true
 * visibel [Boolean]:
 * closeBack [Function]:   visiblevisibel=false 
 * outAnmation [Boolean]:   true
 * queryBack [Function]:   value
 */

export default function NoticeModel({ content, title = "" }) {
  return (
    <div className="my_notice_model an_start_move">
      <span className="title">{title}</span>
      <div
        className="hint_box"
        dangerouslySetInnerHTML={{ __html: content || "" }}
      ></div>
      <button className="bot_btn" type="button" onClick={() => removeModel()}>
        
      </button>
    </div>
  );
}
