import React from "react";
import "./index.less";
import { removeModel } from "./ModelBox";
/**
 *
 * @param {obj} props
 *
 * onRef [Function]: {open:close:} *visibel 
 * mask [Boolean]:  true
 * visibel [Boolean]:
 * closeBack [Function]:   visiblevisibel=false 
 * outAnmation [Boolean]:   true
 * queryBack [Function]:   value
 */

export default function HintModel({ onClick, title = "", msg }) {
  //   
  function submitFn() {
    onClick && onClick();
    removeModel();
  }
  return (
    <div className="my_hint_model an_start_move">
      <span className="title">{title}</span>
      <div className="hint_box">{msg}</div>
      <button className="bot_btn" type="button" onClick={submitFn}>
        
      </button>
    </div>
  );
}
