import React, { useReducer } from "react";
import { Toast } from "antd-mobile";
import { Nav, ModelBox } from "@coms";
import { sellSend } from "@apis/trading";

import "./index.less";

const initialState = {
  num: "", //
  unitPrice: "", //
  low: "", //
  high: "", //
  payType: 0, //alipay   bank 
  dealType: 0, //0  1 
};
function reducer(state, action) {
  switch (action.type) {
    case "change":
      return { ...state, ...action.data };
    default:
      return state;
  }
}

// 
export default function ReleaseDeal({ history }) {
  const [state, dispatch] = useReducer(reducer, initialState);
  //   
  let userdata = JSON.parse(localStorage.userdata);
  //   
  if (userdata.userType === 1 && userdata.certReal !== 1) jumpSetName();
  let options = [
    {
      value: 0,
      label: "",
    },
    {
      value: 1,
      label: "",
    },
  ];
  let optionType = [
    {
      value: 0,
      label: "",
    },
    {
      value: 1,
      label: "",
    },
  ];

  function jumpSetName() {
    ModelBox.open("hintModel", {
      msg: "",
      onClick: () => {
        history.push("/realName");
      },
    });
  }
  //  
  async function submitFn() {
    if (userdata.userType === 1 && userdata.certReal !== 1) {
      jumpSetName();
      return;
    }
    if (!state.unitPrice) {
      Toast.info("");
      return;
    }
    if (!state.num) {
      Toast.info("");
      return;
    }
    if (!state.low) {
      Toast.info("");
      return;
    }
    if (!state.high) {
      Toast.info("");
      return;
    }
    let params = {
      unitPrice: state.unitPrice,
      num: state.num,
      low: state.low,
      high: state.high,
      platform: state.payType,
      dealType: state.dealType,
    };
    try {
      await sellSend(params);
      Toast.success("", 2, () => {
        history.goBack();
      });
    } catch (error) {
      console.log("error", error);
    }
  }
  return (
    <div className="page_box releaseDeal">
      <Nav back title="" />
      <div className="page_menu releaseMenu">
        {/*  */}
        {/* <Input value={userdata.name} label="" disabled /> */}
        {/*  */}
        {/* <Input value={userdata.tel} label="" disabled /> */}
        {/*  */}
        <Input
          value={state.unitPrice}
          label=""
          placeholder=""
          onChange={(text) =>
            dispatch({ type: "change", data: { unitPrice: text } })
          }
        />
        {/*  */}
        <Input
          value={state.num}
          label=""
          placeholder=""
          onChange={(text) => dispatch({ type: "change", data: { num: text } })}
        />
        {/*  */}
        <Input
          value={state.low}
          label=""
          placeholder=""
          onChange={(text) => dispatch({ type: "change", data: { low: text } })}
        />
        {/*  */}
        <Input
          value={state.high}
          placeholder=""
          label=""
          onChange={(text) =>
            dispatch({ type: "change", data: { high: text } })
          }
        />
        {/*  */}
        <Radio
          options={options}
          label=""
          keyName="pay"
          value={state.payType}
          onChange={(val) => {
            dispatch({ type: "change", data: { payType: val } });
          }}
        />
        {/*  */}
        <Radio
          options={optionType}
          label=""
          keyName="deal"
          value={state.dealType}
          onChange={(val) => {
            dispatch({ type: "change", data: { dealType: val } });
          }}
        />
        <button type="button" className="submit_btn" onClick={submitFn}>
          
        </button>
      </div>
    </div>
  );
}

// Input

function Input({ value, label, onChange, disabled = false, placeholder }) {
  return (
    <div className="login_input">
      <div className="label">{label}</div>
      <div className="input_box">
        <input
          type="number"
          placeholder={placeholder}
          value={value}
          disabled={disabled}
          onChange={(e) => onChange && onChange(e.target.value)}
        />
      </div>
    </div>
  );
}

function Radio({ value, onChange, options, label, keyName }) {
  return (
    <div className="login_input">
      <div className="label">{label}</div>
      <div className="right">
        {options.map((v, i) => {
          return (
            <div key={i + "mu_radio"} className="radio_box">
              <label>
                <input
                  type="radio"
                  hidden={true}
                  checked={v.value === value}
                  name={keyName}
                  onChange={() => onChange && onChange(v.value)}
                />
                <div className="ridio_menu" />
                {v.label}
              </label>
            </div>
          );
        })}
      </div>
    </div>
  );
}
