import React, { Component } from "react";
import "./index.less";
import moment from "moment";
import { NextPage, ModelBox } from "@coms";

import { getEntrustList, outDan, setYK, onePinC } from "@apis/trading";
import { Toast } from "antd-mobile";
import sockte from "@serve";
import { connect } from "react-redux";
class LineData extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tabMenuData: [], //
      pages: {
        page: 1,
        size: 10,
        total: 0,
      },
      moneyData: [],
    };
  }
  static getDerivedStateFromProps(nextProps, prevState) {
    let data = nextProps.wsData;
    if (Object.keys(data).length === 0) {
      return null;
    }
    return {
      moneyData: data.map((v) => ({
        name: v.coin,
        newMoney: v.detail.close,
      })),
    };
  }
  componentDidMount() {
    let { onReflash, onPin, type } = this.props;
    onReflash && onReflash(this.reFlashFn);
    onPin && onPin(this.setPin);
    this.searchListFn();
    if (type != 0 && type != 2) {
      sockte.send("DETAIL");
    }
  }
  componentWillUnmount() {
    let { type } = this.props;
    if (type != 0 && type != 2) {
      let name = window.actTab || "home";
      if (name === "wallet") {
        sockte.send(sessionStorage.actType || "BTC");
      }
    }
  }

  //   fn
  searchListFn = async () => {
    let { page, size } = this.state.pages;
    let { status } = this.props;
    try {
      let res = await getEntrustList({ page, size, status });
      this.setState((state) => ({
        tabMenuData:
          page === 1
            ? res.data.records
            : [...state.tabMenuData, ...res.data.records],
        pages: Object.assign({}, state.pages, {
          total: res.data.total,
        }),
      }));
    } catch (error) {
      console.log("", error);
    }
  };
  //   
  reFlashFn = () => {
    this.setState(
      {
        pages: Object.assign({}, this.state.pages, {
          page: 1,
        }),
      },
      this.searchListFn
    );
  };
  //   
  showNewMoney = ({ coinType, stopHigh, stopLow, unitPrice }) => {
    let { moneyData } = this.state;
    let obj = moneyData.find((v) => v.name === coinType);
    if (obj) {
      return obj.newMoney;
    } else {
      return 0;
    }
  };
  //   
  showRage = (nowData, oldData) => {
    if (!nowData && !oldData) {
      return 0;
    }
    return (nowData - oldData).toFixed(2);
  };
  //  
  delCont = (id) => {
    ModelBox.open("hintModel", {
      title: "",
      msg: "",
      onClick: async () => {
        try {
          await outDan({
            contractId: id,
          });
          Toast.success("");
          this.reFlashFn();
        } catch (error) {
          console.log(error);
        }
      },
    });
  };
  // 
  clickNext = (page) => {
    let { pages } = this.state;
    this.setState(
      {
        pages: Object.assign({}, pages, {
          page,
        }),
      },
      this.searchListFn
    );
  };
  //   
  setYKFnshow = (obj) => {
    ModelBox.open("ykModel", {
      high: obj.stopHigh,
      low: obj.stopLow,
      onClick: async ({ up, out }) => {
        try {
          let param = {
            contractId: obj.contractId,
            stopHigh: up,
            stopLow: out,
          };
          !param.stopHigh && delete param.stopHigh;
          !param.stopLow && delete param.stopLow;
          await setYK(param);
          Toast.success("");
          this.reFlashFn();
        } catch (error) {
          console.log(error);
        }
      },
    });
  };
  //   
  setPin = (id, isAll = false) => {
    if (isAll && !this.state.tabMenuData.length) {
      Toast.show("");
      return;
    }
    ModelBox.open("hintModel", {
      title: "",
      msg: isAll ? "" : "",
      onClick: async () => {
        try {
          let param = {
            contractId: id,
            isAll,
          };
          param.isAll && delete param.contractId;
          await onePinC(param);
          Toast.success("");
          this.reFlashFn();
        } catch (error) {
          console.log(error);
        }
      },
    });
  };
  //   type0 
  _renderType_0 = () => {
    let { tabMenuData } = this.state;
    return tabMenuData.map((v, i) => {
      return (
        <div key={i + "menu_i"} className="my_menu_box">
          <div className="title_box">
            {v.coinType}&emsp;
            <span>{moment(v.dealTime).format("MM-DD HH:mm:ss")}</span>
          </div>
          <div className="fn_box">
            <div className="fn_com fn_1">
              <span></span>
              <span>{v.unitPrice}</span>
            </div>
            <div className="fn_com fn_2">
              <span></span>
              <span>{v.num}</span>
            </div>
            <div className="fn_com fn_3">
              <span></span>
              <span onClick={() => this.delCont(v.contractId)}></span>
            </div>
          </div>
          <div className="buy_type">
            :
            {v.dealType === 1 ? "" : ""}
          </div>
        </div>
      );
    });
  };
  // type1 
  _renderType_1 = () => {
    let { tabMenuData } = this.state;
    return tabMenuData.map((v, i) => {
      let nowData = this.showNewMoney(v);
      let isDuo = v.dealType === 1;
      return (
        <div key={i + "menu_i"} className="type2_menu_box">
          <div className="t_grid_box">
            <GridItem label="" value={`${v.coinType}x${v.num}`} />
            <GridItem label="" value={isDuo ? "" : ""} />
            <GridItem label="" value={nowData} />
            <GridItem label="" value={v.unitPrice} />
            <GridItem
              label=""
              value={moment(v.dealTime).format("YYYY-MM-DD HH:mm:ss")}
            />
            <GridItem label="" value={v.stopHigh} />
            <GridItem label="" value={v.stopHigh} />
            <GridItem label="" value={v.stopLow} />
            <GridItem label="" value={v.margin} />
            <GridItem label="" value={v.tfPrice} />
          </div>
          <div className="t_group_btn">
            <button
              type="button"
              onClick={() => this.delCont(v.contractId)}
              className="setPin"
            >
              
            </button>
          </div>
        </div>
      );
    });
  };
  // type2 
  _renderType_2 = () => {
    let { tabMenuData } = this.state;
    return tabMenuData.map((v, i) => {
      let nowData = this.showNewMoney(v);
      let rage = this.showRage(nowData, v.unitPrice);
      let isDuo = v.dealType === 1;
      return (
        <div key={i + "menu_i"} className="type2_menu_box">
          <div className="t_grid_box">
            <GridItem label="" value={`${v.coinType}x${v.num}`} />
            <GridItem label="" value={isDuo ? "" : ""} />
            <GridItem label="" value={nowData} />
            <GridItem label="" value={v.unitPrice} />
            <GridItem
              label=""
              value={moment(v.dealTime).format("YYYY-MM-DD HH:mm:ss")}
            />
            <GridItem label="" value={v.stopHigh} />
            <GridItem label="" value={v.stopLow} />
            <GridItem label="" value={v.tfPrice} />
            <GridItem
              label=""
              value={(rage * v.num).toFixed(2) * (isDuo ? 1 : -1)}
            />
          </div>
          <div className="t_group_btn">
            <button
              type="button"
              onClick={() => this.setYKFnshow(v)}
              className="setIn"
            >
              
            </button>
            <button
              type="button"
              onClick={() => this.setPin(v.contractId)}
              className="setPin"
            >
              
            </button>
          </div>
        </div>
      );
    });
  };
  // type3 
  _renderType_3 = () => {
    let { tabMenuData } = this.state;
    return tabMenuData.map((v, i) => {
      let nowData = this.showNewMoney(v);
      let rage = this.showRage(nowData, v.unitPrice);
      let isDuo = v.dealType === 1;
      return (
        <div key={i + "menu_i"} className="type2_menu_box">
          <div className="t_grid_box">
            <GridItem label="" value={`${v.coinType}x${v.num}`} />
            <GridItem label="" value={isDuo ? "" : ""} />
            <GridItem label="" value={nowData} />
            <GridItem label="" value={v.unitPrice} />
            <GridItem
              label=""
              value={moment(v.dealTime).format("YYYY-MM-DD HH:mm:ss")}
            />
            <GridItem label="" value={v.stopHigh} />
            <GridItem label="" value={v.stopLow} />
            <GridItem label="" value={v.margin} />
            <GridItem label="" value={v.tfPrice} />
            <GridItem
              label=""
              value={(rage * v.num).toFixed(2) * (isDuo ? 1 : -1)}
            />
          </div>
          <div className="t_group_btn">
            <button
              type="button"
              onClick={() => this.setYKFnshow(v)}
              className="setIn"
            >
              
            </button>
            <button
              type="button"
              onClick={() => this.setPin(v.contractId)}
              className="setPin"
            >
              
            </button>
          </div>
        </div>
      );
    });
  };
  // type4 
  _renderType_4 = () => {
    let { tabMenuData } = this.state;
    return tabMenuData.map((v, i) => {
      let isDuo = v.dealType === 1;
      let my_num = (v.closePrice - v.unitPrice) * v.num * (isDuo ? 1 : -1);
      return (
        <div key={i + "menu_i"} className="type2_menu_box">
          <div className="t_grid_box">
            <GridItem label="" value={`${v.coinType}x${v.num}`} />
            <GridItem
              label=""
              value={v.dealType === 1 ? "" : ""}
            />
            <GridItem label="" value={v.unitPrice} />
            <GridItem
              label=""
              value={moment(v.dealTime).format("YYYY-MM-DD HH:mm:ss")}
            />
            <GridItem label="" value={v.closePrice} />
            <GridItem
              label=""
              value={moment(v.closeTime).format("YYYY-MM-DD HH:mm:ss")}
            />
            <GridItem label="" value={v.stopHigh} />
            <GridItem label="" value={v.stopLow} />
            <GridItem label="" value={v.margin} />
            <GridItem label="" value={v.tfPrice} />
            <GridItem label="" value={my_num.toFixed(2)} />
          </div>
        </div>
      );
    });
  };
  // type5 
  _renderType_5 = () => {
    let { tabMenuData } = this.state;
    return tabMenuData.map((v, i) => {
      return (
        <div key={i + "menu_i"} className="type2_menu_box">
          <div className="t_grid_box">
            <GridItem label="" value={`${v.coinType}x${v.num}`} />
            <GridItem
              label=""
              value={v.dealType === 1 ? "" : ""}
            />
            <GridItem label="" value={v.unitPrice} />
            <GridItem
              label=""
              value={moment(v.dealTime).format("YYYY-MM-DD HH:mm:ss")}
            />
            <GridItem label="" value={v.stopHigh} />
            <GridItem label="" value={v.stopLow} />
            <GridItem label="" value={v.margin} />
            <GridItem label="" value={v.tfPrice} />
          </div>
        </div>
      );
    });
  };
  render() {
    let { tabMenuData, pages } = this.state;
    let { type } = this.props;
    return (
      <div className="lineData">
        {/*  */}
        {this["_renderType_" + (type !== undefined ? type : 0)]()}
        {/*  */}
        <NextPage
          page={pages.page}
          size={pages.size}
          total={pages.total}
          onNext={this.clickNext}
          hidden={!tabMenuData.length}
        />
        {/*  */}
        {!tabMenuData.length && <div className="empty_data"></div>}
      </div>
    );
  }
}

// 
function filter(state) {
  return {
    wsData: state.wsData.DETAIL,
  };
}
export default connect(filter)(LineData);

class GridItem extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let { value, label } = this.props;
    return (
      <div className="t_2_box">
        <span className="t_name">{label}</span>
        <span className="t_value">{value || 0}</span>
      </div>
    );
  }
}
