import React, { useState, useEffect } from "react";
// import ReactDom from "react-dom";
import "./index.less";
import { removeModel } from "./ModelBox";
/**
 *
 * @param {obj} props
 *
 * onRef [Function]: {open:close:} *visibel 
 * mask [Boolean]:  true
 * visibel [Boolean]:
 * closeBack [Function]:   visiblevisibel=false 
 * maxLen [Number]: 
 * onInputEnd [Function]: 
 * endClose [Boolean]:   true
 * outAnmation [Boolean]:   true
 */
export default function PassModel({
  maxLen = 6,
  onInputEnd,
  outAnmation = true,
}) {
  const [text, setText] = useState("");
  const [end, setEnd] = useState(false);

  useEffect(() => {
    if (maxLen === text.length) {
      outAnmation && setEnd(true); //  
      setTimeout(() => {
        onInputEnd && onInputEnd(text);
        removeModel();
      }, 200);
    }
    return () => {};
  }, [text]);

  function showItemPass() {
    let domArr = [];
    for (let i = 0; i < maxLen; i++) {
      domArr.push(
        <div
          key={i + "pwd_i"}
          className={`passItme ${text[i] ? "input_out" : ""} ${
            i === text.length ? "next_box_input" : ""
          }`}
        />
      );
    }
    return domArr;
  }

  return (
    <div className={`my_pass_model ${end ? "an_end_move" : "an_start_move"}`}>
      <span></span>
      <label>
        <input
          type="number"
          autoFocus
          onPaste={(e) => e.preventDefault()}
          value={text}
          maxLength={maxLen}
          onChange={(e) => {
            setText(e.target.value);
          }}
        />
        <div className="pwd_item">{showItemPass()}</div>
      </label>
    </div>
  );
}
