import React, { useState } from "react";
import "./index.less";
import { removeModel } from "./ModelBox";
/**
 *
 * @param {obj} props
 *
 * onRef [Function]: {open:close:} *visibel 
 * mask [Boolean]:  true
 * visibel [Boolean]:
 * closeBack [Function]:   visiblevisibel=false 
 * outAnmation [Boolean]:   true
 * queryBack [Function]:   value
 */

export default function YkModel({ onClick, title, high, low }) {
  const [up, setUp] = useState(high || "");
  const [out, setOut] = useState(low || "");
  //   
  function submitFn() {
    onClick &&
      onClick({
        up,
        out,
      });
    removeModel();
  }
  return (
    <div className="my_yk_model an_start_move">
      <span className="title">{title || ""}</span>
      <div className="input_box">
        <div className="login_input">
          <div className="label"></div>
          <div className="input_box">
            <input
              type="number"
              value={up}
              placeholder=""
              autoFocus
              onChange={(e) => setUp(e.target.value)}
            />
          </div>
        </div>
        <div className="login_input">
          <div className="label"></div>
          <div className="input_box">
            <input
              type="number"
              value={out}
              placeholder=""
              onChange={(e) => setOut(e.target.value)}
            />
          </div>
        </div>
      </div>
      <button className="bot_btn" type="button" onClick={submitFn}>
        
      </button>
    </div>
  );
}
