import React, { useState } from "react";
import "./index.less";
import { removeModel } from "./ModelBox";
/**
 *
 * @param {obj} props
 *
 * onRef [Function]: {open:close:} *visibel 
 * mask [Boolean]:  true
 * visibel [Boolean]:
 * closeBack [Function]:   visiblevisibel=false 
 * outAnmation [Boolean]:   true
 * queryBack [Function]:   value
 */

export default function InputModel({
  queryBack,
  title,
  showAll = false,
  allMoney = 0,
}) {
  const [value, setValue] = useState("");
  //   
  function submitFn() {
    queryBack && queryBack(value);
    removeModel();
  }
  return (
    <div className="my_input_model an_start_move">
      <span className="title">{title || ""}</span>
      <div className="input_box">
        <input
          type="number"
          value={value}
          autoFocus
          onChange={(e) => setValue(e.target.value)}
        />
      </div>
      <div className="input_btn_box">
        <button className="input_btn bot_btn" type="button" onClick={submitFn}>
          
        </button>
        {showAll && (
          <button
            className="input_btn all_btn"
            type="button"
            onClick={() => setValue(allMoney)}
          >
            
          </button>
        )}
      </div>
    </div>
  );
}
