import React from "react";
import "./index.less";
import { removeModel } from "./ModelBox";
/**
 *
 * @param {obj} props
 *
 * onRef [Function]: {open:close:} *visibel 
 * mask [Boolean]:  true
 * visibel [Boolean]:
 * closeBack [Function]:   visiblevisibel=false 
 * outAnmation [Boolean]:   true
 * queryBack [Function]:   value
 */

export default function PikerModel({
  value,
  onClick,
  title = "",
  option = [],
}) {
  //   
  function submitFn(v, i) {
    onClick && onClick(v, i);
    removeModel();
  }
  return (
    <div className="my_piker_model an_start_move">
      <span className="title">{title}</span>
      {option.map((v, i) => {
        return (
          <div
            key={i + "os_piker"}
            className={`piker_item ${v.value === value ? "act_item" : ""}`}
            onClick={() => submitFn(v, i)}
          >
            {v.label}
          </div>
        );
      })}
    </div>
  );
}
