import React, { useReducer } from "react";
import "./index.less";
import store from "@store";
import { save_user } from "@store/action";
import { CountDown } from "@coms";
import { testRule } from "@utils/rule";
import { removeModel } from "./ModelBox";
import { setPassPay } from "@apis/api";
import { Toast } from "antd-mobile";

const initialState = {
  code: "",
  loginPass: "",
  payPass: "",
  watchLogin: false, //
  watchPay: false, //
};

function reducer(state, action) {
  switch (action.type) {
    case "change":
      return { ...state, ...action.data };
    default:
      return state;
  }
}
/**
 *
 * @param {obj} props
 *
 * onRef [Function]: {open:close:} *visibel 
 * mask [Boolean]:  true
 * visibel [Boolean]:
 * closeBack [Function]:   visiblevisibel=false 
 * outAnmation [Boolean]:   true
 */
export default function CheckModel({ history }) {
  let userdata = JSON.parse(localStorage.userdata);
  let tel = userdata.tel || userdata.mail;
  let isMail = !userdata.tel;
  let inputArr = [
    {
      label: "",
      name: "tel",
      value: tel,
      disabled: true,
    },
    {
      label: "",
      name: "code",
      cout: true,
      type: "text",
      max: 4,
    },
  ];
  let passArr = [
    {
      label: "",
      name: "loginPass",
      other: "watchLogin",
      max: 12,
    },
    {
      label: "",
      name: "payPass",
      other: "watchPay",
      max: 6,
    },
  ];
  const [state, dispatch] = useReducer(reducer, initialState);

  //   
  async function submitFn() {
    let { payPass, loginPass, code } = state;
    if (!code) {
      Toast.info("");
      return;
    }
    if (!loginPass && !payPass) {
      Toast.info("");
      return;
    }
    if (loginPass && !testRule("password", loginPass)) {
      Toast.info("6-12");
      return;
    }
    if (payPass && !testRule("number", payPass)) {
      Toast.info("");
      return;
    }
    let params = {
      captcha: state.code,
      isTel: !isMail,
      account: tel,
      password: loginPass,
      payPassword: payPass,
    };
    for (const key in params) {
      params[key] || delete params[key];
    }
    try {
      await setPassPay(params);
      Toast.success("", 1);
      if (params.password) {
        localStorage.clear();
        history.push("/login");
      } else if (params.payPassword) {
        if (!userdata.payPassword) {
          userdata.payPassword = "xxxxxx";
          localStorage.userdata = JSON.stringify(userdata);
          store.dispatch(save_user(userdata));
        }
      }
    } catch (error) {
      console.log("error", error);
    } finally {
      removeModel();
    }
  }
  return (
    <div className="my_check_model an_start_move">
      <span className="top_tip"></span>
      {inputArr.map((v, i) => {
        return (
          <div className="inputCom" key={i + "isd"}>
            <div className="label user_label">{v.label}</div>
            <div className="input_box">
              <input
                type={v.type}
                value={v.value || state[v.name]}
                disabled={v.disabled}
                placeholder={"" + v.label}
                onChange={(e) =>
                  dispatch({
                    type: "change",
                    data: {
                      [v.name]: e.target.value.slice(0, v.max),
                    },
                  })
                }
              />
            </div>
            {v.cout && (
              <CountDown
                tel={tel}
                keyType="password"
                isMail={isMail}
                className="my_cont_set"
              />
            )}
          </div>
        );
      })}
      <span className="bot_tip"></span>
      {passArr.map((v, i) => {
        return (
          <div className="inputCom" key={i + "isd"}>
            <div className="label">{v.label}</div>
            <div className="input_box">
              <input
                type={state[v.other] ? "text" : "password"}
                value={state[v.name]}
                placeholder={"" + v.label}
                maxLength={v.max}
                onChange={(e) =>
                  dispatch({
                    type: "change",
                    data: {
                      [v.name]: e.target.value.slice(0, v.max),
                    },
                  })
                }
              />
              <img
                onClick={() =>
                  dispatch({
                    type: "change",
                    data: { [v.other]: !state[v.other] },
                  })
                }
                src={require("@/assets/imgs/icon_eays.png")}
                alt=""
              />
            </div>
          </div>
        );
      })}
      <button className="bot_btn" type="button" onClick={submitFn}>
        
      </button>
    </div>
  );
}
